<template>
  <section>
    <b-container fluid class="files-container">
      <div class="sxd-nav">
        <router-link to="/me">
          <img src="data:image/png;base64,">
          个人中心
        </router-link>
        <span> /</span>
        <img src="data:image/png;base64,">
          <span>互动留言</span>
      </div>
    </b-container>
    <b-container class="leave-con">
      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-small">标题：</label>
        </b-col>
        <b-col sm="10">
          <b-form-input id="input-small" size="sm" type="text" placeholder="输入要留言的标题" />
        </b-col>
      </b-row>

      <b-row class="my-1">
        <b-col sm="2">
          <label for="input-default">留言内容：</label>
        </b-col>
        <b-col sm="10">
          <b-form-textarea id="textarea1" v-model="text" placeholder="输入要留言的具体内容" rows="5" />
          <pre class="mt-3">{{ text }}</pre>
        </b-col>
      </b-row>
    </b-container>
  </section>
</template>

<script>
export default {
  data () {
    return {
      content: '',
      title: ''
    }
  },
  methods: {
    postCid (e) {
      this.content = e.content
      this.title = e.title
    }
  }
}
</script>

<style scoped>

.files-container {
  padding-right: 0px;
  padding-left: 0px;
  background-color: rgb(246, 41, 20);
  width: 100vw;
}

.sxd-nav {
  background-color: #fff;
  /* width: 100vw; */
  padding-left: 0.5rem;
  height: 2.8rem;
  display: flex;
  align-items: center;
  border-bottom: solid 1.4px rgba(0, 0, 0, 0.4);
}
.sxd-nav img {
  margin: -0.3rem 0 0 0.3rem;
}
.sxd-nav a {
  color: rgb(246, 41, 20);
}
.sxd-nav span {
  display: inline-flex;
  margin-left: 0.4rem;
}
.leave-con {
  color: #fff;
  margin-top: 1rem;
}
</style>



